import { MobileScreenShare } from '@mui/icons-material';
import { Button, Tooltip, Typography } from '@mui/material';

export function goToPreview(url) {
  // window.open(`http://localhost:3001/${url}`, '_blank');
  window.open(`https://qrmenu.mmsoftware100.com/${url}`, '_blank');
}

export function PreviewIcon({ url }) {
  return (
    <>
      {/* {url} */}
      <Tooltip title="Preview shop">
        <Button
          onClick={() => goToPreview(url)}
          sx={{ marginRight: '8px' }}
          variant="contained"
          color="inherit"
          startIcon={<MobileScreenShare />}
        >
          Preview Shop
        </Button>
      </Tooltip>
    </>
  );
}


export function cellStyle()  {
  return {backgroundColor:'rgb(0,0,0,0.1)', color:'black'};
}
